@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.ColorPalette {
  width: 100%;
  min-height: 100vh;
  display: flex;
}

.container {
  margin: auto;
  width: 100%;
  max-width: 1062px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.container .card {
  width: 100%;
  max-width: 110px;
  margin: 10px 0;
  text-align: center;
}

.container .card p {
  margin: 12px 0;
  font-weight: 600;
  cursor: pointer;
}

.container .card .box {
  width: 100%;
  height: 100px;
  border-radius: 12px;
  cursor: pointer;
}

#toast {
  visibility: hidden;
  max-width: 50px;
  height: 50px;
  /*margin-left: -125px;*/
  margin: auto;
  background-color: #256d85;
  color: #fff;
  text-align: center;
  border-radius: 2px;

  position: fixed;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 30px;
  font-size: 17px;
  white-space: nowrap;
}
#toast #img {
  width: 50px;
  height: 50px;
  float: left;
  padding: 5px;
  box-sizing: border-box;

  background-color: #2a363b;
  color: #fff;
}
#toast #desc {
  color: #fff;
  padding: 15px;
  font-size: medium;
  font-family: "Montserrat", sans-serif;
  overflow: hidden;
  white-space: nowrap;
}

#toast.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, expand 0.5s 0.5s, stay 3s 1s, shrink 0.5s 2s,
    fadeout 0.5s 2.5s;
  animation: fadein 0.5s, expand 0.5s 0.5s, stay 3s 1s, shrink 0.5s 4s,
    fadeout 0.5s 4.5s;
}

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes expand {
  from {
    min-width: 50px;
  }
  to {
    min-width: 250px;
  }
}

@keyframes expand {
  from {
    min-width: 50px;
  }
  to {
    min-width: 250px;
  }
}
@-webkit-keyframes stay {
  from {
    min-width: 250px;
  }
  to {
    min-width: 250px;
  }
}

@keyframes stay {
  from {
    min-width: 250px;
  }
  to {
    min-width: 250px;
  }
}
@-webkit-keyframes shrink {
  from {
    min-width: 250px;
  }
  to {
    min-width: 50px;
  }
}

@keyframes shrink {
  from {
    min-width: 250px;
  }
  to {
    min-width: 50px;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 60px;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 60px;
    opacity: 0;
  }
}
